<template>
  <form @submit.prevent="submit">
    <div class="form-group text-dark">
      <label for="fullName">Nama</label>
      <input
        type="text"
        class="form-control col-12 col-md-12 h-25"
        maxlength="250"
        :placeholder="fullName.name"
        disabled
      />
      <p></p>
      <label for="inputAddress">Kepada<small> (max 50 karakter) </small></label>
      <input
        type="text"
        class="form-control col-12 col-md-12 h-25"
        maxlength="50"
        v-model="form.to"
        required
      />
      <p><small> e.g "PT. JOHN DOE"</small></p>

      <label for="inputMatkul"
        >Mata Kuliah<small> (max 50 karakter) </small></label
      >
      <input
        type="text"
        class="form-control col-12 col-md-12 h-25"
        maxlength="50"
        v-model="form.course"
        required
      />
      <p><small> e.g "Fisika Dasar"</small></p>
    </div>
    <div class="form-group text-dark">
      <label for="inputAddress"
        >Alamat Perusahaan<small> (max 150 karakter) </small></label
      >
      <textarea
        type="text"
        class="form-control col-12 col-md-5 h-25"
        v-model="form.address"
        maxlength="150"
        required
      />
      <p>
        <small
          >e.g"Jl.Cibaduyut Raya No.66, Cibadyut, Kec.Bojongloa Kidul"</small
        >
      </p>
    </div>

    <button type="submit" class="btn btn-primary btn-success">
      Pengajuan Sekarang
    </button>
  </form>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "SIPD",
  inject: ["notyf"],
  data() {
    return {
      form: {
        id_letter_categories: localStorage.getItem("idLetters"),
        username: null,
        to: null,
        address: null,
        course: null,
      },
      name: {
        name: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      fullName: "auth/users",
    }),
  },
  methods: {
    ...mapActions({
      setDataRiwayatSip: "riwayatpengajuanlimit/riwayatsuratjenislimit",
    }),
    number: function (index) {
      return index + 1;
    },
    getNameAndNpm() {
      (this.name = this.fullName.name),
        (this.form.username = this.fullName.username);
    },
    setRiwayatToView() {
      this.setDataRiwayatSip();
    },
    async submit() {
      try {
        let dataSip = {
          id_letter_categories: this.form.id_letter_categories,
          username: this.form.username,
          course: this.form.course,
          to: this.form.to,
          address: this.form.address,
        };
        let response = await axios.post("/siakad/send-letters", dataSip);

        this.setRiwayatToView();
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.setRiwayatToView({});
    this.getNameAndNpm();
  },
};
</script>

