<template>
  <form @submit.prevent="submit">
    <div class="form-group text-dark">
      <label for="fullName">Nama</label>
      <input
        type="text"
        class="form-control col-12 col-md-12 h-25"
        maxlength="250"
        :placeholder="fullName.name"
        disabled
      />
      <p></p>
      <label for="inputAddress"
        >Kepada Yth.<small> (max 50 karakter) </small></label
      >
      <input
        type="text"
        class="form-control col-12 col-md-12 h-25"
        maxlength="50"
        v-model="form.to"
        required
      />
      <p><small> e.g "Bpk. John Doe"</small></p>

      <label for="inputKelas">Kelas</label>
      <input
        type="text"
        class="form-control col-12 col-md-12 h-25"
        maxlength="50"
        v-model="form.class"
        required
      />
      <p></p>
    </div>
    <button type="submit" class="btn btn-primary btn-success">
      Pengajuan Sekarang
    </button>
  </form>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "smaktif",
  inject: ["notyf"],
  data() {
    return {
      form: {
        id_letter_categories: localStorage.getItem("idLetters"),
        username: null,
        class: null,
      },
      name: {
        name: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      fullName: "auth/users",
    }),
  },
  methods: {
    ...mapActions({
      setDataRiwayatSip: "riwayatpengajuanlimit/riwayatsuratjenislimit",
    }),
    setRiwayatToView() {
      this.setDataRiwayatSip();
    },
    getNameAndNpm() {
      (this.name = this.fullName.name),
        (this.form.username = this.fullName.username);
    },
    async submit() {
      try {
        let dataSip = {
          id_letter_categories: this.form.id_letter_categories,
          username: this.form.username,
          class: this.form.class,
        };
        let response = await axios.post("/siakad/send-letters", dataSip);

        this.setRiwayatToView();
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.setRiwayatToView({});
    this.getNameAndNpm();
  },
};
</script>

