<template>
  <div class="container-fluid p-0">
    <div class="row">
      <div class="col-12 col-md-6">
        <h4 class="title mb-3">Pengajuan Surat</h4>
        <select
          @change="getHalPengajuan($event)"
          class="form-control col-12 col-md-12 mb-3"
          id="exampleFormControlSelect1"
        >
          <option disable>Pilih Pengajuan</option>

          <option
            v-for="tipesurat in getTypeLetter"
            :key="tipesurat.id_letter_categories"
            :value="tipesurat.id_letter_categories"
          >
            {{ tipesurat.name }}
          </option>
        </select>
        <div class="skip">
          <Skip v-if="pilihTipeSurat == 1" />
          <Skerjapraktek v-if="pilihTipeSurat == 2" />
          <Sipd v-if="pilihTipeSurat == 3" />
          <SmhsAktif v-if="pilihTipeSurat == 4" />
          <Sobservasi v-if="pilihTipeSurat == 5" />
          <Swawancara v-if="pilihTipeSurat == 6" />
          <Scuti v-if="pilihTipeSurat == 7" />
          <Sout v-if="pilihTipeSurat == 8" />
        </div>
      </div>

      <div class="col-12 col-md-6 mb-4">
        <h4 class="title mb-3">Riwayat Pengajuan Surat Terbaru</h4>
        <TableRiwayat />
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Skip from "@/components/pengajuan/surat/SKIP";
import Skerjapraktek from "@/components/pengajuan/surat/KerjaPraktek";
import Sipd from "@/components/pengajuan/surat/SIPD";
import SmhsAktif from "@/components/pengajuan/surat/SuratMhsAktif";
import Sobservasi from "@/components/pengajuan/surat/Observasi";
import Swawancara from "@/components/pengajuan/surat/Wawancara";
import Scuti from "@/components/pengajuan/surat/Cuti";
import Sout from "@/components/pengajuan/surat/Keluar";
import TableRiwayat from "@/components/pengajuan/TableRiwayatSurat";
export default {
  name: "TipePengajuan",

  components: {
    TableRiwayat,
    Skip,
    Skerjapraktek,
    Sipd,
    SmhsAktif,
    Sobservasi,
    Swawancara,
    Sout,
    Scuti,
  },
  data() {
    return {
      pilihTipeSurat: "selected",
      tipesurat: [],
      suratName: [],
    };
  },
  computed: {
    ...mapGetters({
      getTypeLetter: "jenisSurat/p_surat_get",
    }),
  },
  methods: {
    ...mapActions({
      typeLetter: "jenisSurat/p_surat",
    }),

    funGetTypeLetter() {
      this.typeLetter();
    },
    getHalPengajuan(event) {
      this.pilihTipeSurat = event.target.value[0];
      this.suratName = event.target.value[2];
      localStorage.setItem("idLetters", this.pilihTipeSurat);
    },
  },

  mounted() {
    this.funGetTypeLetter();
  },
};
</script>

