<template>
  <table class="table" id="datatables-responsive">
    <thead>
      <td>No</td>
      <td>Nama Surat</td>
      <td>Status</td>
    </thead>
    <tbody>
      <tr v-for="(riwayat, index) in getDataRiwayat" :key="number(index, 10)">
        <td>{{ number(index) }}</td>
        <td v-if="riwayat.to !== 'NULL'">{{ riwayat.to }}</td>
        <td v-else>Surat Mahasiswa Aktif</td>

        <td class="check-status" v-if="riwayat.status === 0">
          <div>
            <span class="badge badge-warning badge-lg">Menunggu</span>
          </div>
        </td>
        <td class="check-status" v-if="riwayat.status === 1">
          <div>
            <span class="badge badge-success badge-lg">Diterima</span>
          </div>
        </td>
        <td class="check-status" v-if="riwayat.status === 2">
          <div>
            <span class="badge badge-danger badge-lg">Ditolak</span>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: "TableRiwayat",
  data(){
      return{
          riwayat: []
      }
  },
  computed:{
      ...mapGetters({
          getDataRiwayat: "riwayatpengajuanlimit/getRiwayatLimit"
      })
  }, methods: {
    number: function (index) {
      return index + 1;
    },
    mounted(){
    }
}
}
</script>

